<template>
<div class="timetable__wrapper" ref="main">
    <MainHeader filterType = "allTime" />
    <div v-for="day, index in dates" :key="index" class="content">
        <DateHeader :date="day" :buttonNeeded="false"/>
        <Timetable :id="index" :date="day" :infoAboutDay="infoAboutDay[index]" />
    </div>
</div>
</template>
<script>
import Timetable from './Timetable.vue'
import * as dayjs from 'dayjs'
import 'dayjs/locale/ru'
import MainHeader from './MainHeader.vue'
import DateHeader from './DateHeader.vue'
export default {
    components: {
        Timetable,
        MainHeader,
        DateHeader
    },
    data(){
        return{
            dates: [], 
            today: dayjs().locale('ru'),
            infoAboutDay: [],
            facility: JSON.parse(localStorage.getItem('facility')),
        }
    },
    created(){
        this.setDate()
    },
    mounted(){
        this.scrollToToday();
    },
    methods:{
        setDate(){
            dayjs.locale('ru')
            var day = dayjs()
            day = day.subtract(7, 'day')
            for(let i = 0;i<30;i++){
                this.infoAboutDay.push(this.setInfo(day.day()))
                this.dates.push(day)
                day = day.add(1, 'day')
            }
        },
        setInfo(dayOfWeek){
            let infoFieldsDaily = {}
            for(const field in this.facility.fields){
                infoFieldsDaily = {...infoFieldsDaily, [field]:this.facility.fields[field].weekdays[dayOfWeek]}
            }
            return infoFieldsDaily
        }, 
        scrollToToday(){
            const TIMETABLE__HEIGHT = 195
            const TIMETABLE__BEFORE = 7
            window.scrollTo(0, TIMETABLE__HEIGHT*TIMETABLE__BEFORE)
            window.onbeforeunload = function () {
                window.scrollTo(0, TIMETABLE__HEIGHT*TIMETABLE__BEFORE)
            }
        }
    }

}
</script>
<style lang = "scss">
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.content{
    width: max-content;
}

.timetable__wrapper{
    overflow-x: scroll;
    &:first-child{
        margin-top: 93px;
    }
}

</style>