<template>
    <div class="singleDay__main">
        <MainHeader filterType = "oneDay" />
        <div class="content">
            <DateHeader :date="day" :buttonNeeded="true" @prev='prev' @next='next' />
            <Timetable :id="id" :date="day" :infoAboutDay="setInfo(day.day())" :facility="facility" :key="day.toString()"/>
        </div>
        <div class="bookings">
            <div class="bookings__title">
                <p>Брони: {{bookings.length}}</p>
            </div>
            <div v-for="booking, index in bookings" :key="index" class="bookings__content">
                <BookingMiniature :info="booking" />
            </div>
        </div> 
    </div>
</template>
<script>
import dayjs from 'dayjs'
import 'dayjs/locale/ru'
import MainHeader from './MainHeader.vue'
import Timetable from './Timetable.vue'
import DateHeader from './DateHeader'
import BookingMiniature from './BookingMiniature.vue'
export default {
    data(){
        return {
            facility: JSON.parse(localStorage.getItem('facility')),
            day: dayjs().locale('ru'),
            id: 7,
            bookings: [],
            orders: JSON.parse(localStorage.getItem('orderInfo'))
        }
    },
    beforeUpdate(){
        this.getDailyOrders();
    },
    methods:{
        setInfo(dayOfWeek){
            let infoFieldsDaily = {}
            for(const field in this.facility.fields){
                infoFieldsDaily = {...infoFieldsDaily, [field]:this.facility.fields[field].weekdays[dayOfWeek]}
            }
            return infoFieldsDaily
        },
        getDailyOrders(){
            this.bookings = [];
            for(const order in this.orders){
                for(const booking in this.orders[order]['bookings']){
                    if(this.orders[order]['bookings'][booking].date===this.day.format('DD-MM-YYYY')){
                        this.bookings.push({'booking': this.orders[order]['bookings'][booking], "client": this.orders[order]['client']})
                    }
                }
            }
        },
        prev(){
            this.day = this.day.subtract(1,'day');
            this.id-=1;
        },
        next(){
            this.day = this.day.add(1,'day');
            this.id+=1;
        }
    },
    components: {
      Timetable, 
      MainHeader, 
      DateHeader,
      BookingMiniature
    }
    
}
</script>
<style lang="scss" scoped>
.singleDay__main{
    overflow: auto;
    height: 100vh;
    background: #E5E5E5;
    &:first-child{
        margin-top: 93px;
    }
}
.bookings{
    position: sticky;
    left: 0;
    right: 0;
    width: 100vw;
}
.bookings::-webkit-scrollbar{
    width: 0;
}
.bookings__title{
    background: #fff;
    height: 52px;
}
</style>
