<template>
    <div class="orderInfo">
        <div class="orderInfo__inner">
            <div class="orderInfo__info">
                <div class="order__date flex">
                    <p class="order__type">Разовая бронь</p>
                    <p class="date__value">{{info.date.format('DD MMM')}}</p>
                </div>
                <div class="order__details flex">
                    <p class="details__key">Детали</p>
                    <div class="details__value">
                        <p>{{info.time}}, {{info.date.format('dddd')}}</p>
                        <p>{{info.duration}}({{info.cost}} 	₸)</p>
                    </div>
                </div>
                <div class="order__field flex">
                    <p class="field__key">Площадка</p>
                    <p class="field__value">№{{info.field}}</p>
                </div>
                <div class="order__cost flex">
                    <p class="cost__key">Общая сумма</p>
                    <p class="cost__value">{{info.cost}} ₸</p>
                </div>
                <div class="order__discount flex">
                    <p class="discount__key">Скидка</p>
                    <p class="discount__value">{{discount}} ₸</p>
                </div>
                <div class="order__remainingPaylaod flex">
                    <p class="remainingPayload__key">Остаток</p>
                    <p class="remaininPayload__value">{{info.cost-discount}} ₸</p>
                </div>
            </div>
            <div  @click="addDiscount()" class="discount__button">
                Добавить скидку
            </div>
            <div class="discount__input" v-if="discountAdd">
                <label for="discount">Пожалуйста, введите сумму скидки</label>
                <input type="number" id="discount" v-model="discount" max = info.cost>
                <button @click="cancelDiscount()">Отмена</button>
                <button @click="discountAdd=false">Ок</button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props: ['info'],
    data(){
        return{
            discount: 0,
            discountAdd: false
        }
    },
    methods: {
        addDiscount(){
            this.discountAdd = true
        },
        cancelDiscount(){
            this.discount = 0;
            this.discountAdd = false;
        }
    }
}
</script>
<style lang="scss" scoped>
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.orderInfo{
    background: #fff;
    padding: 16px 15px;
    margin-top: 16px;       
}
.flex{
    display: flex;
    margin-bottom: 12px;
}
.flex>*{
    width: 50%;
}
p, label, .discount__button{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
}
.discount__button{
    width: 100%;
    height: 28px;
    background: rgba(0, 0, 0, 0.04);
    border: 0.3px solid rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center; 
    align-items: center;
}
</style>
